.button-scan {
  position: absolute;
  top: 0;
  right: 0;
  height: 50px;
  width: 50px;
  background-color: gray;
  margin: 30px;
  border-radius: 50%;

  svg {
    width: 100%;
    height: 100%;
    padding: 12px;
  }
}
